import React from "react";
import {HashRouter as Router, Route, Switch} from "react-router-dom";
import logo from "./logo.svg";
import "./App.css";
import Header from "./components/Header.js";
import { StepsDetails, StepsTerms, StepsTest } from "./components/Steps.js";
import Details from "./components/Details.js";
import Terms from "./components/Terms.js";
import Test from "./components/Test.js";
import "./components/css/style.css";
import "./components/css/style-responsive.css";
import history from './components/history';
import { Redirect } from 'react-router-dom'
function App(props) {
  return (
    <div>
          <Router history={history}>
                <Switch>
                    <Route exact path="/" {...props} component={Details}></Route>
                    <Route exact path="/terms" render={(props) => (
                            localStorage.getItem('SudentDetail') != undefined
                                ? <Terms {...props}/>
                                : <Redirect exact to='/' />)} /> 
                    <Route exact path="/test" render={(props) => (
                            localStorage.getItem('SudentDetail') != undefined
                                ? <Test {...props}/>
                                : <Redirect exact to='/' />)} /> 
                </Switch>
            </Router>
    </div>
  );
}

export default App;
