import React, { Component } from "react";
import Header from './Header';
import {StepsTest} from './Steps';
import Calculator from './Calculator';
import axios from 'axios';
import {FaShareAlt, FaCalculator} from 'react-icons/fa';
import SweetAlert from 'react-bootstrap-sweetalert';
const btnWidth = {
  width: "200px",
};

const topRight = {
  borderTopRightRadius: "0px",
};

const botRight = {
  borderBottomRightRadius: "0px",
};

const topLeft = {
  borderTopLeftRadius: "0px",
};

const botLeft = {
  borderBottomLeftRadius: "0px",
};

class Test extends Component {
  state = {
    primaryDetails:null,
    questionCount:0,
    calculatorDisplay:false,
    studentSelections:[],
    popUp:false,
    countdown:null,
    min_counter:30,
    sec_counter:0
  }
  componentDidMount() {
    axios.get(`/question/`)
      .then(res => {
        console.log(res.data)
        this.setState({primaryDetails:res.data,
          // ,questionCount:res.data.Questions.length,
          countdown:(res.data.TestInfo.duration.split(':')[1]) * 60 * 1000
        })
        // setTimeout(this.handleSubmit, Math.floor(res.data.TestInfo.duration.split(":")[1]*60*1000))
        let studentSelection = [...this.state.studentSelections]
        res.data.Questions.map((item)=><>
        {item.choices.map((sub_item)=>
        <>
        {studentSelection.push({id:sub_item.id,value:""})}
        {this.setState({studentSelections:studentSelection})}
        </>
        )}
        </>)
      })
      
  }
timerId = setInterval(()=> {
    this.setState({countdown:this.state.countdown-1000})
    var min = Math.floor(this.state.countdown / (60 * 1000));
    var sec = Math.floor((this.state.countdown - min * 60 * 1000) / 1000);
  
    this.state.countdown < 0?this.handleSubmit():this.setState({min_counter:min,sec_counter:sec})
  }, 1000);
onConfirm=()=>{return window.location.href = 'http://technohubbit.pythonanywhere.com/'; }

handleSubmit=()=>{
  let studentDetail = JSON.parse(localStorage.getItem("SudentDetail"));
  console.log(studentDetail)
  axios.put(`/question/`, { "studentDetail":studentDetail,
                                                  "studentSelections":this.state.studentSelections,
                                                    })
      .then(res => {
        console.log(res);
        if(res.status===200){this.setState({popUp:true})
                            localStorage.clear()}
      })
}

studentSelections = (e) =>{
  this.state.studentSelections.find(v => v.id == e.target.id).value = e.target.value;
  console.log(this.state.studentSelections)
}


  render() 
  
  {
    console.log(this.state.studentSelections)
    return (
    <div>
      <Header />
      <StepsTest />
      {this.state.popUp?<SweetAlert success title="Success!" onConfirm={this.onConfirm}>
        You Successfully completed your test.<br/>Please checkout your mail for result.
      </SweetAlert>:null}
      {this.state.calculatorDisplay?<Calculator/>:null}
      <div class="like_share">
          <div className="share_box" >
          <FaCalculator class="share_icon" onClick={()=>{this.setState({calculatorDisplay:!this.state.calculatorDisplay})}}/>
          </div>
      </div>
      <div class="container body">
        <div class="timer_container">
          <div id="timer">
            <div class="box mins">
              <div id="min_value" class="value" style={topRight}>{this.state.min_counter}</div>
              <div class="text" style={botRight}>
                Minute(s)
              </div>
            </div>
            <div class="box secs">
              <div id="sec_value" class="value" style={topLeft}>{this.state.sec_counter}</div>
              <div class="text" style={botLeft}>
                Second(s)
              </div>
            </div>
          </div>
        </div>
        <br />
        <br />
        <div class="container_form">
          <form id="quiz">

            {this.state.primaryDetails!=null | undefined?<>
              {this.state.primaryDetails.Questions.map((item, i)=><>
              <div key={item.id}>
              <div class="input_form">
                <input value={item.question} disabled />
                <label>Question{i+1}.</label>
              </div>
              <br />
              {item.choices.map((sub_item)=>
              
                <div class="options" id={sub_item.id} onChange={this.studentSelections}>
                {/* <input type="radio" name="q1" value="blank" checked hidden /> */}
                <input type="radio" name={item.question} id={sub_item.id} value="option1" />
                &nbsp;&nbsp;{sub_item.option1}
                <br />
                <input type="radio" name={item.question} id={sub_item.id} value="option2" />
                &nbsp;&nbsp;{sub_item.option2}
                <br />
                <input type="radio" name={item.question} id={sub_item.id} value="option3" />
                &nbsp;&nbsp;{sub_item.option3}
                <br />
                <input type="radio" name={item.question} id={sub_item.id} value="option4" />
                &nbsp;&nbsp;{sub_item.option4}
                <br />
                
              </div>
              )}
            </div>
            <br />
              </>)}
            </>
            :<h2>loading questions...</h2>}
          </form>
          <br />
          <div class="btn_box">
            <button
              type="submit"
              class="btn"
              title="Submit Your Test"
              style={btnWidth}
              onClick={this.handleSubmit}
            >
              <span>Submit Test </span>
              <i class="fas fa-check"></i>
            </button>
          </div>
          <br />
          <br />
        </div>
      </div>
    </div>
  );
}
}
export default Test;
